.head-icon-font{
    font-size: 12px;
    margin-top: -30px;
    font-weight: bold;
}
.head-icon{
    float: left;
    text-align: center;
    cursor: pointer;
    transition-duration: 100ms;
}
.head-icon:hover{
    transform:translateY(-3px) scale(1.05,1.05);
   
}

.rss{
    
    right: 0px;
    background-color: bisque;
    width: 200px;
    height: 100px;
    
    z-index: 9999;
    position: absolute;
    transform-origin: 100% 0%;
}

.artical-head-info{
    float: left;
    height: 200px;
    width: 400px;
    margin-top: 10px;
}
.artical-head-info-right{
    float: left;
    height: 200px;
    width: 400px;
    margin-top: 10px;
    margin-left: 100px;
   
}

.artical-head-info-t{
    clear: both;
    float: left;
    margin-top: 10px;
   height: 200px;
   width: 400px;
}
.timer{
    background-color:rgb(19, 174, 235);
    width: 100%;
}
.messageBox{
    
    border-style:solid;
    margin-top: 50px;
    border-color: rgb(68, 67, 67);
    border-width: 3px 1px 1px 1px;
}
.messageLine{
    border-style: solid;
    
    border-color: rgb(68, 67, 67);
    border-width: 1px 0px 0px 0px;
}